import React from 'react';
import ReactEcharts from 'echarts-for-react';

// Base64 image strings for bar colors
const currPrincipalBg = 'data:image/png;base64,';

const newPrincipalBg = 'data:image/png;base64,';

const oldIntBg = 'data:image/png;base64,';

const newIntBg = 'data:image/png;base64,';

const PayoffAmt = ({ getCurrencyFormat, principal, offsets, paths, user, colors}) => {

  let principalCurrBg = new Image();
  principalCurrBg.src = currPrincipalBg;

  let principalNewBg = new Image();
  principalNewBg.src = newPrincipalBg;

  let intCurrBg = new Image();
  intCurrBg.src = oldIntBg;

  let intNewBg = new Image();
  intNewBg.src = newIntBg;

  const options = {
    id: 'payoffAmt',
    show: false,
    animation: true,
    tooltip : {
        trigger: 'axis',
        axisPointer : {
          type : 'none'
        },
        padding: [6, 10],
        formatter: (params) => {
          // console.log('tooltip params');
          // console.log(params);
          if (params[0].data.value || params[1].data.value) {
            // console.log('params exist');
            return '<strong>' + params[0].axisValueLabel + '</strong><br>&nbsp;' +
              // params[0].marker + '&nbsp;' +
              (!!params[0].data.value ?
                getCurrencyFormat(params[0].data.value, true) :
                '$0' ) +
              ' PRINCIPAL<br>&nbsp;' +
              // params[1].marker + '&nbsp;' +
              (!!params[1].data.value ?
                getCurrencyFormat(params[1].data.value, true) :
                '$0' ) +
              ' INTEREST';
          } else {
            return '';
          }
        },
        textStyle: {
          fontFamily: 'Vasarely',
          fontSize: 16,
          color: '#000',

        },
        backgroundColor: '#FFEBAE',
        borderColor: '#000',
        borderWidth: 1,
        extraCssText: 'border-radius:0;'
    },
    legend: {
        data: ['OLD TOTAL PAID', 'NEW TOTAL PAID']
    },
    grid: {
      left: 0,
      top: 20,
      width: '100%',
      height: 70,
      containLabel: true
    },
    xAxis:  {
        type: 'value',
        show: false
    },
    yAxis: {
        type: 'category',
        data: ['NEW TOTAL PAID', 'OLD TOTAL PAID'],
        show: true,
        axisLine: {
          show: false
        },
        axisTick: {
          show: false,
          alignWithLabel: true,
        },
        axisLabel: {
          fontFamily: 'Vasarely',
          fontSize: 18,
          verticalAlign: 'middle',
          padding: [0, 0, 5, 23],
        }
    },
    barWidth: 10,
    // barGap: '1%',
    series: [
      {
        type: 'bar',
        stack: 'yes',
        data: [
          { // New principal
            value: user.loanPrincipalNew, // principal,
            itemStyle: {
              // color: colors.principalNew,
              color: {
                image: principalNewBg,
                repeat: 'repeat'
              },
            },
            label: {
              borderColor: 'transparent',
              normal: {
                color: colors.darkFont,
                fontFamily: 'Vasarely',
                fontSize: 16,
                show: true,
                position: 'bottom',
                padding: 0,
                formatter: (el) => {
                  // console.log(el.value);
                  if (el.value >= 1) {
                    return getCurrencyFormat(el.value, true);
                  } else {
                    return '';
                  }
                }
              }
            }
          }, // end new principal.
          { // Old principal
            value: user.loanPrincipalCurr, // principal,
            itemStyle: {
              // color: colors.principalOld,
              color: {
                image: principalCurrBg, // principalCurrBg,
                repeat: 'repeat'
              },
            },
            label: {
              // value: totalPaidCurr,
              borderColor: 'transparent',
              normal: {
                color: colors.darkFont,
                fontFamily: 'Vasarely',
                fontSize: 16,
                show: true,
                position: 'top', // 'insideBottomRight',
                padding: 0,
                formatter: (el) => {
                  // console.log(el.value);
                  if (el.value >= 1) {
                    return getCurrencyFormat(el.value, true);
                  } else {
                    return '';
                  }
                }
              },
            }
          } // End old principal
        ],
      },
      {// Begin interest
        type: 'bar',
        stack: 'yes',
        data: [{ // New interest
          value: user.totalIntNew,
          itemStyle: {
            // color: colors.interestNew,
            color: {
              image: intNewBg,
              repeat: 'repeat'
            },
          },
          label: {
            borderColor: 'transparent',
            normal: {
              show: true,
              color: colors.darkFont,
              fontFamily: 'Vasarely',
              fontSize: 16,
              position: 'bottom', // 'insideTopRight',
              padding: 0,
              formatter: (el) => {
                // console.log(el.value);
                if (el.value >= 1) {
                  return getCurrencyFormat(el.value, true);
                } else {
                  return '';
                }
              }
            }
          },
        }, // end new interest
        { // Old interest
          value: user.totalIntCurr,
          itemStyle: {
            // color: colors.interestOld,
            color: {
              image: intCurrBg,
              repeat: 'repeat'
            },
          },
          label: {
            borderColor: 'transparent',
            normal: {
              color: colors.darkFont,
              fontFamily: 'Vasarely',
              fontSize: 16,
              show: true,
              position: 'top',
              padding: 0,
              formatter: (el) => {
                // console.log(el.value);
                if (el.value >= 1) {
                  return getCurrencyFormat(el.value, true);
                } else {
                  return '';
                }
              }
            }
          },
        }// end old interest
      ],
    }, // End interest
    ]
  };

// opts={{renderer: 'svg'}}

  return (
    <div className="payoff-amt-parent">
      <ReactEcharts
        option={options}
        style={{height: '100px', width: '100%'}}
      />
    </div>
  );
}

export default PayoffAmt;
